@charset "UTF-8";
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0; }

*,
*:before,
*:after {
  box-sizing: border-box; }

:focus,
:active {
  outline: none; }

a:focus,
a:active {
  outline: none; }

body {
  overflow-x: hidden; }

nav,
footer,
header,
aside {
  display: block; }

input,
button,
textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit; }

/* Header */
.page {
  background-color: #F5EBDD;
  min-height: 100vh;
  margin: 10px;
  border-radius: 15px; }
  .page__info {
    margin-top: 40px;
    display: flex;
    flex-direction: column; }
    @media only screen and (min-width: 768px) {
      .page__info {
        flex-direction: row;
        align-items: center; } }
    @media only screen and (min-width: 1440px) {
      .page__info {
        padding: 0 37px; } }
    .page__info h1 {
      font-family: 'Roboto';
      font-weight: 900;
      font-size: 40px;
      color: #151414;
      margin-bottom: 10px;
      animation-duration: 1.5s;
      /* don't forget to set a duration! */ }
      @media only screen and (min-width: 1440px) {
        .page__info h1 {
          font-size: 66px; } }
    .page__info p {
      font-family: 'Montserrat';
      font-weight: 500;
      font-size: 18px;
      color: #151414; }
      @media only screen and (min-width: 1440px) {
        .page__info p {
          font-size: 22px;
          max-width: 400px; } }
    .page__info--img img {
      width: 100%; }
    @media only screen and (min-width: 768px) {
      .page__info--block {
        width: 50%; } }
    .page__info--block a {
      display: flex;
      align-items: center;
      justify-content: center;
      max-width: 270px;
      width: 100%;
      height: 56px;
      border-radius: 100px;
      text-align: center;
      background-color: #FF4387;
      color: #fff;
      font-family: 'Montserrat';
      font-weight: 700;
      font-size: 17px;
      margin: 35px auto 0 auto;
      transition: .3s; }
      @media only screen and (min-width: 768px) {
        .page__info--block a {
          margin: 35px 0; } }
      .page__info--block a:hover {
        background-color: #d8316e; }

.container {
  padding: 0 20px; }
  @media only screen and (min-width: 1440px) {
    .container {
      padding: 0 40px; } }

.header {
  padding: 20px 0; }
  .header__inner {
    position: relative;
    display: flex;
    justify-content: space-between; }
  .header__nav {
    display: none; }
    @media only screen and (min-width: 1440px) {
      .header__nav {
        display: block; } }
  .header__nav--list {
    display: flex; }
  .header__nav--item {
    margin-right: 45px; }
    .header__nav--item:last-child {
      margin-right: 0; }
  .header__nav--link {
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 17px;
    color: #151414;
    transition: .3s; }
    .header__nav--link:hover {
      color: #313131;
      opacity: .6; }
  .header__phone {
    display: none; }
    @media only screen and (min-width: 1440px) {
      .header__phone {
        display: block; } }
    .header__phone a {
      font-family: 'Montserrat';
      font-weight: bold;
      font-size: 16px;
      color: #151414; }

.logo__link {
  font-family: 'Puritan';
  color: #30242A;
  font-size: 30px;
  font-weight: 700; }

.burger span::after {
  content: '';
  position: absolute;
  top: 10px;
  right: 0;
  width: 30px;
  height: 3px;
  background-color: #151414; }

.burger span::before {
  content: '';
  position: absolute;
  top: 20px;
  right: 0;
  width: 30px;
  height: 3px;
  background-color: #151414; }

@media only screen and (min-width: 1440px) {
  .burger {
    display: none; } }

/* About */
.about {
  min-height: 100vh;
  background-color: #FFCBBA;
  margin: 10px;
  border-radius: 15px; }
  .about__inner {
    padding-top: 50px; }
    @media only screen and (min-width: 1440px) {
      .about__inner {
        display: flex;
        padding: 170px 37px 0 37px; } }
  @media only screen and (min-width: 1440px) {
    .about__title {
      width: 50%; } }
  .about__title h4 {
    font-family: 'Montserrat';
    font-weight: bold;
    font-size: 15px;
    text-transform: uppercase;
    color: #313131;
    margin-bottom: 20px; }
  .about__title p {
    font-family: "Roboto";
    font-weight: 900;
    font-size: 32px;
    color: #151414;
    margin-bottom: 30px; }
    @media only screen and (min-width: 1440px) {
      .about__title p {
        font-size: 50px;
        max-width: 400px; } }
  .about__info {
    padding-bottom: 25px; }
    @media only screen and (min-width: 1440px) {
      .about__info {
        width: 50%; } }
    .about__info--item {
      padding: 25px;
      background: #FFFFFF;
      border-radius: 20px;
      line-height: 29px;
      border-bottom-right-radius: 0;
      margin-bottom: 20px;
      position: relative; }
      .about__info--item::after {
        content: url(../img/arc.svg);
        position: absolute;
        right: 0;
        bottom: -25px; }
      .about__info--item p {
        font-family: 'Montserrat';
        font-weight: 500;
        font-size: 18px;
        color: #313131; }
        @media only screen and (min-width: 1440px) {
          .about__info--item p {
            font-size: 20px; } }
      .about__info--item span {
        font-weight: bold;
        background-color: #F5EBDD;
        border-radius: 3px; }
    .about__info--cirlce {
      display: flex;
      justify-content: flex-end; }
      .about__info--cirlce span {
        width: 70px;
        height: 70px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fff;
        font-family: 'Puritan';
        font-weight: bold;
        font-size: 18px;
        line-height: 20px;
        color: #30242A; }
        @media only screen and (min-width: 1440px) {
          .about__info--cirlce span {
            width: 100px;
            height: 100px;
            font-size: 30px; } }
  .about__img {
    display: none; }
    @media only screen and (min-width: 1440px) {
      .about__img {
        display: block; } }

/* Work */
